import {api} from '/js/api';
import {Languages} from "/js/util";

api({
	getTypes: 'phototype/getallphototypes',
	getTags: 'photo/GetAllPhotoTitles',
	'getPhotos!post': 'photo/QueryPhotosByTitleAndTypeId',
	getPhotosByTag: 'photo/QueryPhotosByTitle',
});

interface SelOption {
	value: string | number;
	text: string;
	children?: SelOption[]
}

let typesArr: SelOption[];
let tagArr: HashMap<SelOption[]> = {};

$.when(
	api.getTypes(data => {
		//typesArr = data as Array;
		typesArr = data.map((v: any): SelOption => {
			return {
				value: v.id,
				text: v.name,
				children: v.childPhotoTypes.map((sub: any): SelOption => {
					return {
						value: sub.id,
						text: sub.name,
					}
				})
			}
		});
		console.log(typesArr);
	}),

	api.getTags(data => {
		for (let key in data) {
			let arr = [];
			for (let i = 0, l = data[key].length; i < l; i++) {
				arr[i] = {
					value: 0,
					text: data[key][i],
				}
			}
			tagArr[key] = arr;
		}
	})
).then(() => {

	let picker1 = new mui.PopPicker({
		buttons: [Languages.package.cancel, Languages.package.ok]
	});
	let sel1 = document.getElementById('sel1');


	let picker2 = new mui.PopPicker({
		buttons: [Languages.package.cancel, Languages.package.ok]
	});
	let sel2 = document.getElementById('sel2');

	let picker3 = new mui.PopPicker({
		buttons: [Languages.package.cancel, Languages.package.ok]
	});
	let sel3 = document.getElementById('sel3');

	picker1.setData(typesArr);
	sel1.innerText = typesArr[0].text;
	sel1.addEventListener('tap', function (event) {
		picker1.show(function (items) {
			sel1.innerText = items[0].text;
			console.log(items);

			let opts2 = items[0].children || [];
			picker2.setData(opts2);
			sel2.innerText = opts2.length ? opts2[0].text : '';

			if (opts2.length) {
				let opts3 = tagArr[opts2[0].value] || [];
				picker3.setData(opts3);

				let tag = opts3.length ? opts3[0].text : '';
				sel3.innerText = tag;
				getPhotos(opts2[0].value, tag);
			}
			else {
				picker3.setData([]);
				sel3.innerText = '';
				getPhotos(items[0].value, '');
			}


			//返回 false 可以阻止选择框的关闭
			//return false;
		});
	}, false);

	picker2.setData(typesArr[0].children);
	sel2.innerText = typesArr[0].children[0].text;
	sel2.addEventListener('tap', function (event) {
		picker2.show(function (items) {
			sel2.innerText = items[0].text;

			let opts3 = tagArr[items[0].value] || [];
			picker3.setData(opts3);
			let tag = opts3.length ? opts3[0].text : '';
			sel3.innerText = tag;
			getPhotos(items[0].value, tag);
		});
	});


	picker3.setData(tagArr[typesArr[0].children[0].value]);
	let sel3Opt0 = tagArr[typesArr[0].children[0].value];
	if (sel3Opt0 && sel3Opt0.length) {
		sel3.innerText = sel3Opt0[0].text;
		getPhotos(typesArr[0].children[0].value, sel3Opt0[0].text);
	}
	else {
		getPhotos(typesArr[0].children[0].value, '');
	}


	sel3.addEventListener('tap', function (event) {
		picker3.show(function (items) {
			let tag = items[0].text;
			sel3.innerText = tag;

			let opt2 = picker2.getSelectedItems()[0];
			getPhotos(opt2.value, tag);
		});
	});

	//sel1.dispatchEvent(new CustomEvent( 'tap'));
});

let divTag = document.getElementById('tag');
let divUl = document.getElementById('wholeWrapper');
let imgRender = template.compile(document.getElementById('tpl-img').innerHTML);

function getPhotos(typeId: number | string, tag?: string = '') {
	api.getPhotos({typeId, title: tag}, data => {
		divTag.innerText = tag;
		divUl.innerHTML = imgRender(data);
	});
}


function searchPhoto(title: string) {
	api.getPhotosByTag({title}, data => {
		divTag.innerText = title;
		divUl.innerHTML = imgRender(data);
	})
}


document.getElementById('iptSearch').addEventListener('keypress', function (evt) {
	if (evt.keyCode === 13 && this.value) {
		searchPhoto(this.value);
	}
}, false);
